import { Component } from '@angular/core';
import {App, Events, IonicPage, NavController, NavParams, ToastController, ViewController} from 'ionic-angular';

/**
 * Generated class for the EditPwdPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name:'editPwd'
})
@Component({
  selector: 'page-edit-pwd',
  templateUrl: 'edit-pwd.html',
})
export class EditPwdPage {
  oldPwd:string = '';
  newPwd:string = '';
  constructor(public navCtrl: NavController,
              public toastCtrl: ToastController,
              public events: Events,
              public viewCtrl: ViewController,
              public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditPwdPage');
  }
  editPwd(){
    if(Math.random()>0.5){
      let toast = this.toastCtrl.create({
        message: '修改密码失败，请重试！',
        duration: 1500,
        position:'middle',
      });
      toast.present();
    }else{
      let toast = this.toastCtrl.create({
        message: '修改密码成功',
        duration: 1500,
        position:'middle',
      });
      toast.present();
      this.events.publish('toLogin');
    }
  }
}



